*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    font-size: 100%;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
}

body {
    font-family: "ProximaNova", sans-serif;
    overflow-x: hidden;
    color: $black;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
    transition: .3s linear;
}

select {
    border: none;
}

select:active,
select:focus {
    outline: none;
}

select:focus > option {
    outline: 0;
}

input:focus,
textarea:focus {
    border: none;
    outline: none;
}

h1, h2, h3, h4 {
    font-weight: 400;
}

h1 {
    font-weight: bold;
    font-size: 45px;
	line-height: 55px;
    color: $black;
}

h2 {
    font-size: 56px;
}

h3 {
    font-size: 36px;
}

h4 {
    font-size: 32px;
}

p {
    font-weight: 300;
    font-size: 24px;
    line-height: 1.67;
    margin: 0;
}

.container {
    max-width: 1220px;
    padding: 0 20px;
    margin: 0 auto;
}

.desktop-hidden {
    
    @media (min-width: 541px) {
        display: none;
    }
}

.mobile-hidden {
    
    @media (max-width: 540px) {
        display: none;
    }
}