.vacancy {
	padding-bottom: 54px;
	
	@media (max-width: 540px) {
		padding-bottom: 23px;
		border-bottom: 1px solid #CBCBCB;
	}
	
	&_title {
		margin-bottom: 42px;
		
		@media (max-width: 540px) {
			margin-bottom: 31px;
		}
	}
	
	&_list {
		margin-bottom: 56px;
		
		@media (max-width: 540px) { 
			margin-bottom: 25px;
		}
	}
	
	&_list-group {
		margin-bottom: 39px;
		
		@media (max-width: 540px) {
			margin-bottom: 23px;
		}
		
		&:last-child {
			margin-bottom: 0;
		}
	}
	
	&_list-title {
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		margin-bottom: 12px;
		
		@media (max-width: 540px) {
			font-size: 24px;
			line-height: 29px;
		}
	}
	
	&_mail {
		font-size: 28px;
		line-height: 34px;
		text-align: center;
		
		@media (max-width: 540px) {
			font-size: 20px;
			line-height: 24px;
		}
		
		a {
			font-weight: bold;
			color: $light_green;
		}
	}
}